'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import type { AgencyTelephonySettings, SubscriptionPlan } from '@/lib/telephony/types'

// ==========================================
// AGENCY TELEPHONY SETTINGS PAGE
// Level 2: Agency-level settings
// Enterprise plan agencies can add their own markup
// ==========================================

interface AgencyInfo {
  id: string
  name: string
  subscriptionPlan: SubscriptionPlan
  canSetMarkup: boolean
}

export default function AgencyTelephonySettingsPage() {
  const [activeTab, setActiveTab] = useState<'overview' | 'pricing' | 'subaccounts' | 'features' | 'compliance'>('overview')
  const [settings, setSettings] = useState<AgencyTelephonySettings | null>(null)
  const [agencyInfo, setAgencyInfo] = useState<AgencyInfo | null>(null)
  const [platformMarkup, setPlatformMarkup] = useState<number>(5)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error' | 'info'; text: string } | null>(null)

  useEffect(() => {
    fetchSettings()
  }, [])

  const fetchSettings = async () => {
    setLoading(true)
    try {
      // Fetch agency settings
      const res = await fetch('/api/v1/agency/telephony/settings')
      const data = await res.json()
      
      if (data.success) {
        setSettings(data.data.settings)
        setAgencyInfo(data.data.agencyInfo)
        setPlatformMarkup(data.data.platformMarkup || 5)
      } else {
        // Initialize with defaults
        setSettings(getDefaultSettings())
        // Mock agency info for demo
        setAgencyInfo({
          id: 'demo-agency',
          name: 'Demo Agency',
          subscriptionPlan: 'professional', // Change to 'enterprise' to see markup options
          canSetMarkup: false
        })
      }
    } catch (err) {
      console.error('Error fetching settings:', err)
      setSettings(getDefaultSettings())
      setAgencyInfo({
        id: 'demo-agency',
        name: 'Demo Agency',
        subscriptionPlan: 'professional',
        canSetMarkup: false
      })
    } finally {
      setLoading(false)
    }
  }

  const getDefaultSettings = (): AgencyTelephonySettings => ({
    id: '',
    agencyId: '',
    subscriptionPlan: 'professional',
    canSetMarkup: false,
    agencyMarkupPercent: 0,
    agencyMarkupEnabled: false,
    promoMarkupPercent: null,
    promoMarkupStartDate: null,
    promoMarkupEndDate: null,
    promoMarkupDescription: null,
    defaultDailySmsLimit: null,
    defaultDailyCallLimit: null,
    defaultPhoneNumberType: 'local',
    defaultPhoneNumberCountry: 'US',
    voiceEnabled: true,
    smsEnabled: true,
    mmsEnabled: true,
    powerDialerEnabled: true,
    autoProvisionSubaccounts: true,
    autoProvisionPhoneNumber: false,
    smsBounceWarningThreshold: null,
    smsBounceSuspendThreshold: null,
    dailySmsLimitMax: null,
    dailyCallLimitMax: null,
    hasDedicatedTwilioSubaccount: false,
    twilioSubaccountId: null,
    billingEmail: null,
    billingContactName: null,
    isActive: true,
    activatedAt: null,
    suspendedAt: null,
    suspensionReason: null,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  })

  const saveSettings = async () => {
    if (!settings) return

    setSaving(true)
    setMessage(null)

    try {
      const res = await fetch('/api/v1/agency/telephony/settings', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(settings)
      })

      const data = await res.json()
      if (data.success) {
        setSettings(data.data)
        setMessage({ type: 'success', text: 'Settings saved successfully!' })
      } else {
        setMessage({ type: 'error', text: data.error || 'Failed to save settings' })
      }
    } catch (err) {
      setMessage({ type: 'error', text: 'Network error' })
    } finally {
      setSaving(false)
    }
  }

  const isEnterprise = agencyInfo?.subscriptionPlan === 'enterprise'

  if (loading) {
    return (
      <div className="p-6 max-w-5xl mx-auto">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 max-w-5xl mx-auto">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <div className="flex items-center gap-3">
            <Link href="/agency/settings" className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-arrow-left"></i>
            </Link>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              <i className="fas fa-phone-volume text-blue-600"></i>
              Telephony Settings
            </h1>
            <span className={`px-2 py-1 text-xs font-medium rounded ${
              isEnterprise 
                ? 'bg-indigo-100 text-indigo-700' 
                : 'bg-blue-100 text-blue-700'
            }`}>
              {agencyInfo?.subscriptionPlan === 'enterprise' && <i className="fas fa-crown mr-1"></i>}
              {agencyInfo?.subscriptionPlan?.charAt(0).toUpperCase()}{agencyInfo?.subscriptionPlan?.slice(1)} Plan
            </span>
          </div>
          <p className="text-gray-500 text-sm mt-1 ml-8">
            Configure telephony settings for your agency and sub-accounts
          </p>
        </div>
        <div className="flex gap-3">
          <button
            onClick={saveSettings}
            disabled={saving}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 flex items-center gap-2"
          >
            {saving ? (
              <>
                <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                Saving...
              </>
            ) : (
              <>
                <i className="fas fa-save"></i>
                Save Settings
              </>
            )}
          </button>
        </div>
      </div>

      {/* Message */}
      {message && (
        <div className={`mb-6 p-4 rounded-lg flex items-center gap-3 ${
          message.type === 'success' ? 'bg-green-50 border border-green-200 text-green-700' : 
          message.type === 'error' ? 'bg-red-50 border border-red-200 text-red-700' :
          'bg-blue-50 border border-blue-200 text-blue-700'
        }`}>
          <i className={`fas ${
            message.type === 'success' ? 'fa-check-circle' : 
            message.type === 'error' ? 'fa-exclamation-circle' :
            'fa-info-circle'
          }`}></i>
          {message.text}
        </div>
      )}

      {/* Enterprise Upgrade Banner (if not Enterprise) */}
      {!isEnterprise && (
        <div className="mb-6 bg-gradient-to-r from-indigo-500 to-purple-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center">
                <i className="fas fa-crown text-2xl"></i>
              </div>
              <div>
                <h3 className="font-semibold text-lg">Unlock Agency Markup with Enterprise</h3>
                <p className="text-white/80 text-sm">
                  Add your own markup on top of platform pricing and increase your revenue per sub-account.
                </p>
              </div>
            </div>
            <Link
              href="/agency/billing/upgrade"
              className="px-4 py-2 bg-white text-indigo-600 rounded-lg font-medium hover:bg-white/90 transition-colors"
            >
              Upgrade to Enterprise
            </Link>
          </div>
        </div>
      )}

      {/* 3-Tier Architecture Info */}
      <div className="mb-6 p-4 bg-blue-50 border border-blue-200 rounded-xl">
        <div className="flex items-start gap-4">
          <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center flex-shrink-0">
            <i className="fas fa-sitemap text-blue-600"></i>
          </div>
          <div className="flex-1">
            <h3 className="font-semibold text-blue-900 mb-2">How Pricing Works</h3>
            <div className="flex items-center gap-2 text-sm">
              <span className="px-2 py-1 bg-purple-100 text-purple-700 rounded font-medium">
                Platform {platformMarkup}%
              </span>
              {isEnterprise && settings?.agencyMarkupEnabled && (
                <>
                  <span className="text-gray-400">+</span>
                  <span className="px-2 py-1 bg-indigo-100 text-indigo-700 rounded font-medium">
                    Your Markup {settings?.agencyMarkupPercent || 0}%
                  </span>
                  <span className="text-gray-400">=</span>
                  <span className="px-2 py-1 bg-green-100 text-green-700 rounded font-medium">
                    Total {platformMarkup + (settings?.agencyMarkupPercent || 0)}%
                  </span>
                </>
              )}
              {!isEnterprise && (
                <span className="text-blue-600 text-xs">
                  (Your sub-accounts pay Twilio + {platformMarkup}% platform markup)
                </span>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="flex gap-1 mb-6 bg-gray-100 p-1 rounded-lg overflow-x-auto">
        {[
          { id: 'overview', label: 'Overview', icon: 'fas fa-home' },
          { id: 'pricing', label: 'Pricing & Markup', icon: 'fas fa-dollar-sign', enterprise: true },
          { id: 'subaccounts', label: 'Sub-Account Defaults', icon: 'fas fa-users' },
          { id: 'features', label: 'Features', icon: 'fas fa-toggle-on' },
          { id: 'compliance', label: 'Compliance', icon: 'fas fa-shield-alt' }
        ].map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as typeof activeTab)}
            className={`px-4 py-2 rounded-md flex items-center gap-2 whitespace-nowrap transition-colors ${
              activeTab === tab.id
                ? 'bg-white text-blue-600 shadow-sm'
                : 'text-gray-600 hover:text-gray-900'
            } ${tab.enterprise && !isEnterprise ? 'opacity-50' : ''}`}
          >
            <i className={tab.icon}></i>
            {tab.label}
            {tab.enterprise && !isEnterprise && (
              <i className="fas fa-lock text-xs text-gray-400"></i>
            )}
          </button>
        ))}
      </div>

      {/* Content */}
      <div className="bg-white rounded-xl shadow-sm border">
        {activeTab === 'overview' && settings && (
          <OverviewTab 
            settings={settings} 
            agencyInfo={agencyInfo} 
            platformMarkup={platformMarkup}
          />
        )}
        {activeTab === 'pricing' && settings && (
          <PricingTab 
            settings={settings} 
            onChange={setSettings}
            isEnterprise={isEnterprise}
            platformMarkup={platformMarkup}
          />
        )}
        {activeTab === 'subaccounts' && settings && (
          <SubAccountDefaultsTab settings={settings} onChange={setSettings} />
        )}
        {activeTab === 'features' && settings && (
          <FeaturesTab settings={settings} onChange={setSettings} />
        )}
        {activeTab === 'compliance' && settings && (
          <ComplianceTab settings={settings} onChange={setSettings} />
        )}
      </div>
    </div>
  )
}

// ==========================================
// OVERVIEW TAB
// ==========================================

function OverviewTab({
  settings,
  agencyInfo,
  platformMarkup
}: {
  settings: AgencyTelephonySettings
  agencyInfo: AgencyInfo | null
  platformMarkup: number
}) {
  const isEnterprise = agencyInfo?.subscriptionPlan === 'enterprise'
  const totalMarkup = platformMarkup + (isEnterprise && settings.agencyMarkupEnabled ? settings.agencyMarkupPercent : 0)

  return (
    <div className="p-6 space-y-6">
      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="bg-gray-50 rounded-lg p-4">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
              <i className="fas fa-users text-blue-600"></i>
            </div>
            <span className="text-gray-500 text-sm">Sub-Accounts</span>
          </div>
          <p className="text-2xl font-bold text-gray-900">--</p>
        </div>
        
        <div className="bg-gray-50 rounded-lg p-4">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 bg-green-100 rounded-lg flex items-center justify-center">
              <i className="fas fa-phone text-green-600"></i>
            </div>
            <span className="text-gray-500 text-sm">Phone Numbers</span>
          </div>
          <p className="text-2xl font-bold text-gray-900">--</p>
        </div>
        
        <div className="bg-gray-50 rounded-lg p-4">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 bg-purple-100 rounded-lg flex items-center justify-center">
              <i className="fas fa-percentage text-purple-600"></i>
            </div>
            <span className="text-gray-500 text-sm">Total Markup</span>
          </div>
          <p className="text-2xl font-bold text-gray-900">{totalMarkup}%</p>
        </div>
        
        <div className="bg-gray-50 rounded-lg p-4">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 bg-amber-100 rounded-lg flex items-center justify-center">
              <i className="fas fa-dollar-sign text-amber-600"></i>
            </div>
            <span className="text-gray-500 text-sm">MTD Usage</span>
          </div>
          <p className="text-2xl font-bold text-gray-900">$--</p>
        </div>
      </div>

      {/* Status Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className={`p-4 rounded-lg border ${
          settings.isActive 
            ? 'bg-green-50 border-green-200' 
            : 'bg-red-50 border-red-200'
        }`}>
          <div className="flex items-center gap-3">
            <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
              settings.isActive ? 'bg-green-100' : 'bg-red-100'
            }`}>
              <i className={`fas ${settings.isActive ? 'fa-check text-green-600' : 'fa-times text-red-600'}`}></i>
            </div>
            <div>
              <p className={`font-medium ${settings.isActive ? 'text-green-800' : 'text-red-800'}`}>
                Telephony {settings.isActive ? 'Active' : 'Inactive'}
              </p>
              <p className={`text-sm ${settings.isActive ? 'text-green-600' : 'text-red-600'}`}>
                {settings.isActive 
                  ? 'Your sub-accounts can use telephony features' 
                  : 'Telephony is disabled for your agency'}
              </p>
            </div>
          </div>
        </div>

        <div className={`p-4 rounded-lg border ${
          isEnterprise 
            ? 'bg-indigo-50 border-indigo-200' 
            : 'bg-gray-50 border-gray-200'
        }`}>
          <div className="flex items-center gap-3">
            <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
              isEnterprise ? 'bg-indigo-100' : 'bg-gray-100'
            }`}>
              <i className={`fas fa-crown ${isEnterprise ? 'text-indigo-600' : 'text-gray-400'}`}></i>
            </div>
            <div>
              <p className={`font-medium ${isEnterprise ? 'text-indigo-800' : 'text-gray-800'}`}>
                {isEnterprise ? 'Enterprise Plan' : `${agencyInfo?.subscriptionPlan?.charAt(0).toUpperCase()}${agencyInfo?.subscriptionPlan?.slice(1)} Plan`}
              </p>
              <p className={`text-sm ${isEnterprise ? 'text-indigo-600' : 'text-gray-500'}`}>
                {isEnterprise 
                  ? 'You can add your own markup to sub-accounts' 
                  : 'Upgrade to Enterprise to add your own markup'}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Pricing Breakdown */}
      <div className="border rounded-lg p-4">
        <h3 className="font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <i className="fas fa-calculator text-blue-600"></i>
          Sub-Account Pricing Breakdown
        </h3>
        
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="border-b">
                <th className="text-left py-2 text-gray-500">Item</th>
                <th className="text-right py-2 text-gray-500">Twilio Base</th>
                <th className="text-right py-2 text-gray-500">Platform (+{platformMarkup}%)</th>
                {isEnterprise && settings.agencyMarkupEnabled && (
                  <th className="text-right py-2 text-gray-500">Your Markup (+{settings.agencyMarkupPercent}%)</th>
                )}
                <th className="text-right py-2 text-gray-500 font-medium">Sub-Account Pays</th>
              </tr>
            </thead>
            <tbody>
              <tr className="border-b">
                <td className="py-3">SMS (outbound)</td>
                <td className="text-right text-gray-500">$0.0075</td>
                <td className="text-right text-purple-600">$0.0004</td>
                {isEnterprise && settings.agencyMarkupEnabled && (
                  <td className="text-right text-indigo-600">${(0.0075 * settings.agencyMarkupPercent / 100).toFixed(4)}</td>
                )}
                <td className="text-right font-medium text-green-600">
                  ${(0.0075 * (1 + totalMarkup / 100)).toFixed(4)}
                </td>
              </tr>
              <tr className="border-b">
                <td className="py-3">Voice Call (per min)</td>
                <td className="text-right text-gray-500">$0.0140</td>
                <td className="text-right text-purple-600">$0.0007</td>
                {isEnterprise && settings.agencyMarkupEnabled && (
                  <td className="text-right text-indigo-600">${(0.014 * settings.agencyMarkupPercent / 100).toFixed(4)}</td>
                )}
                <td className="text-right font-medium text-green-600">
                  ${(0.014 * (1 + totalMarkup / 100)).toFixed(4)}
                </td>
              </tr>
              <tr>
                <td className="py-3">Phone Number (monthly)</td>
                <td className="text-right text-gray-500">$1.00</td>
                <td className="text-right text-purple-600">${(1.00 * platformMarkup / 100).toFixed(2)}</td>
                {isEnterprise && settings.agencyMarkupEnabled && (
                  <td className="text-right text-indigo-600">${(1.00 * settings.agencyMarkupPercent / 100).toFixed(2)}</td>
                )}
                <td className="text-right font-medium text-green-600">
                  ${(1.00 * (1 + totalMarkup / 100)).toFixed(2)}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// PRICING TAB - AGENCY MARKUP (ENTERPRISE ONLY)
// ==========================================

function PricingTab({
  settings,
  onChange,
  isEnterprise,
  platformMarkup
}: {
  settings: AgencyTelephonySettings
  onChange: (settings: AgencyTelephonySettings) => void
  isEnterprise: boolean
  platformMarkup: number
}) {
  const [showPromoSettings, setShowPromoSettings] = useState(!!settings.promoMarkupPercent)

  // Calculate total markup
  const agencyMarkup = isEnterprise && settings.agencyMarkupEnabled ? settings.agencyMarkupPercent : 0
  const totalMarkup = platformMarkup + agencyMarkup

  if (!isEnterprise) {
    return (
      <div className="p-6">
        <div className="text-center py-12">
          <div className="w-16 h-16 bg-indigo-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <i className="fas fa-lock text-indigo-600 text-2xl"></i>
          </div>
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Enterprise Feature</h3>
          <p className="text-gray-500 max-w-md mx-auto mb-6">
            Agency markup is only available on the Enterprise plan. Upgrade to add your own 
            markup on top of platform pricing and increase your revenue.
          </p>
          <Link
            href="/agency/billing/upgrade"
            className="inline-flex items-center gap-2 px-6 py-3 bg-indigo-600 text-white rounded-lg font-medium hover:bg-indigo-700 transition-colors"
          >
            <i className="fas fa-crown"></i>
            Upgrade to Enterprise
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 space-y-6">
      {/* Agency Markup */}
      <div>
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
            <i className="fas fa-percentage text-indigo-600"></i>
            Agency Markup
            <span className="px-2 py-1 bg-indigo-100 text-indigo-700 text-xs font-medium rounded">
              Enterprise
            </span>
          </h3>
          <label className="flex items-center gap-2">
            <span className="text-sm text-gray-600">Enable Markup</span>
            <input
              type="checkbox"
              checked={settings.agencyMarkupEnabled}
              onChange={(e) => onChange({ ...settings, agencyMarkupEnabled: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-indigo-600"
            />
          </label>
        </div>

        <div className="bg-indigo-50 border border-indigo-200 rounded-lg p-4 mb-4">
          <div className="flex items-start gap-3">
            <i className="fas fa-money-bill-wave text-indigo-600 mt-1"></i>
            <div>
              <p className="font-medium text-indigo-800">Stacked Markup Model</p>
              <p className="text-sm text-indigo-700">
                Your markup is added ON TOP of the platform markup ({platformMarkup}%). 
                Your sub-accounts will pay Twilio base price + platform markup + your agency markup.
              </p>
            </div>
          </div>
        </div>

        {settings.agencyMarkupEnabled ? (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Markup Input */}
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Your Agency Markup
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={settings.agencyMarkupPercent}
                    onChange={(e) => onChange({ ...settings, agencyMarkupPercent: parseFloat(e.target.value) || 0 })}
                    className="w-full px-4 py-3 pr-12 border-2 border-indigo-200 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 text-xl font-semibold"
                    min="0"
                    max="50"
                    step="0.5"
                  />
                  <span className="absolute right-4 top-1/2 -translate-y-1/2 text-gray-400 text-xl">%</span>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  Added on top of {platformMarkup}% platform markup
                </p>
              </div>

              {/* Quick Presets */}
              <div>
                <label className="block text-xs font-medium text-gray-500 mb-2">Quick Presets</label>
                <div className="flex flex-wrap gap-2">
                  {[5, 10, 15, 20, 25].map(percent => (
                    <button
                      key={percent}
                      onClick={() => onChange({ ...settings, agencyMarkupPercent: percent })}
                      className={`px-3 py-1.5 rounded-full text-sm font-medium transition-colors ${
                        settings.agencyMarkupPercent === percent
                          ? 'bg-indigo-600 text-white'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                    >
                      {percent}%
                    </button>
                  ))}
                </div>
              </div>
            </div>

            {/* Visual Breakdown */}
            <div className="bg-gray-50 p-4 rounded-lg">
              <p className="text-sm font-medium text-gray-700 mb-3">Markup Stack Visualization</p>
              
              <div className="space-y-2 mb-4">
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 bg-gray-300 rounded"></div>
                  <span className="text-sm text-gray-600">Twilio Base Price</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 bg-purple-500 rounded"></div>
                  <span className="text-sm text-gray-600">Platform Markup ({platformMarkup}%)</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 bg-indigo-500 rounded"></div>
                  <span className="text-sm text-gray-600">Your Markup ({settings.agencyMarkupPercent}%)</span>
                </div>
              </div>

              {/* Visual Bar */}
              <div className="h-8 rounded-lg overflow-hidden flex mb-2">
                <div 
                  className="bg-gray-300 flex items-center justify-center text-xs font-medium"
                  style={{ width: `${100 / (1 + totalMarkup / 100)}%` }}
                >
                  Base
                </div>
                <div 
                  className="bg-purple-500 text-white flex items-center justify-center text-xs font-medium"
                  style={{ width: `${(platformMarkup / (100 + totalMarkup)) * 100}%` }}
                >
                  +{platformMarkup}%
                </div>
                {settings.agencyMarkupPercent > 0 && (
                  <div 
                    className="bg-indigo-500 text-white flex items-center justify-center text-xs font-medium"
                    style={{ width: `${(settings.agencyMarkupPercent / (100 + totalMarkup)) * 100}%` }}
                  >
                    +{settings.agencyMarkupPercent}%
                  </div>
                )}
              </div>
              
              <p className="text-center text-sm font-medium text-green-600">
                Total: {totalMarkup}% above Twilio
              </p>
            </div>
          </div>
        ) : (
          <div className="text-center py-8 bg-gray-50 rounded-lg">
            <i className="fas fa-toggle-off text-4xl text-gray-300 mb-3"></i>
            <p className="text-gray-500">Enable markup to add your own percentage on top of platform pricing</p>
          </div>
        )}
      </div>

      {/* Promotional Pricing */}
      {settings.agencyMarkupEnabled && (
        <div className="border-t pt-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
              <i className="fas fa-tag text-orange-500"></i>
              Promotional Markup
            </h3>
            <button
              onClick={() => {
                setShowPromoSettings(!showPromoSettings)
                if (!showPromoSettings) {
                  onChange({ 
                    ...settings, 
                    promoMarkupPercent: null,
                    promoMarkupStartDate: null,
                    promoMarkupEndDate: null,
                    promoMarkupDescription: null
                  })
                }
              }}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                showPromoSettings
                  ? 'bg-orange-100 text-orange-700'
                  : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
            >
              {showPromoSettings ? 'Cancel Promo' : 'Create Promo'}
            </button>
          </div>

          {showPromoSettings && (
            <div className="bg-orange-50 border border-orange-200 rounded-lg p-4">
              <p className="text-sm text-orange-700 mb-4">
                <i className="fas fa-info-circle mr-2"></i>
                During the promotional period, this markup will replace your standard agency markup.
              </p>
              
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Promo Markup %</label>
                  <div className="relative">
                    <input
                      type="number"
                      value={settings.promoMarkupPercent || ''}
                      onChange={(e) => onChange({ 
                        ...settings, 
                        promoMarkupPercent: e.target.value ? parseFloat(e.target.value) : null 
                      })}
                      className="w-full px-4 py-2 pr-8 border rounded-lg focus:ring-2 focus:ring-orange-500"
                      min="0"
                      max="50"
                      step="0.5"
                      placeholder="e.g., 5"
                    />
                    <span className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400">%</span>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Start Date</label>
                  <input
                    type="datetime-local"
                    value={settings.promoMarkupStartDate?.slice(0, 16) || ''}
                    onChange={(e) => onChange({ 
                      ...settings, 
                      promoMarkupStartDate: e.target.value ? new Date(e.target.value).toISOString() : null 
                    })}
                    className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">End Date</label>
                  <input
                    type="datetime-local"
                    value={settings.promoMarkupEndDate?.slice(0, 16) || ''}
                    onChange={(e) => onChange({ 
                      ...settings, 
                      promoMarkupEndDate: e.target.value ? new Date(e.target.value).toISOString() : null 
                    })}
                    className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Description</label>
                  <input
                    type="text"
                    value={settings.promoMarkupDescription || ''}
                    onChange={(e) => onChange({ ...settings, promoMarkupDescription: e.target.value || null })}
                    className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                    placeholder="e.g., New Year Special"
                  />
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  )
}

